import React, {Component} from 'react';

class ComputedStyle extends Component {
    constructor(props) {
        super(props);
    }
    getStyle() {
        let style = '';
        // console.log(this.props.data);
        Object.keys(this.props.data).forEach((key,i) => {
            // console.log(key)
            // console.log(this.props.data[key])
            Object.keys(this.props.data[key]).forEach((subKey, j) => {
                // console.log(this.props.data[key][subKey]);
                const curColor = this.props.data[key][subKey].color
                this.props.data[key][subKey].dep.forEach(itm => {
                    style += `#FR-${itm.id} {fill: ${curColor}}`
                })
            })
        });
        return style
    }

    render()
    {
        return (
            <style>
                {this.getStyle()}
            </style>
        );
    }

}
export default ComputedStyle;