import React, {Component} from 'react';
import XLSX from 'xlsx';

import { createRoot } from 'react-dom/client';

import './App.css';

import France from './France';
import DepartmentsList from './DepartmentsList';
import ComputedStyle from './ComputedStyle';
import Caption from './Caption';

import list from './list';

class App extends Component {

    constructor(props) {
        super(props);
        this.state = {
            data : {},
            list: list,
            error: null,
            csm: [],
            colors: [
                ['#991b1b','#dc2626', '#f87171','#fca5a5',], //red
                ['#c2410c','#ea580c', '#fb923c','#fdba74',], //orange
                ['#3f6212','#65a30d', '#a3e635','#bef264',], //vert degueu
                ['#1e40af','#2563eb', '#60a5fa','#93c5fd',], //blue
                ['#6b21a8','#9333ea', '#c084fc','#d8b4fe',], //pink
            ],
            selectedDep: null,
            depName: null
        }
        this.handleSelectedDep = this.handleSelectedDep.bind(this)
    }

    async readXlsFile(e) {
        this.setState({error: null});

        const file = e.target.files[0];
        const data = await file.arrayBuffer();
        try {
            const workbook = XLSX.read(data);
            const worksheet = workbook.Sheets[workbook.SheetNames[0]];
            const raw_data = XLSX.utils.sheet_to_json(worksheet, {header: 1});
            if (raw_data.length) {
                raw_data.shift(); //on sup la premiere ligne du excel
                // console.log(raw_data);
                const data = {};
                raw_data.forEach(itm => {
                    const id = itm[0];
                    const departmentName = itm[1];
                    const regionName = itm[2];
                    const csm = itm[3].toLowerCase();
                    const sale = itm[4].toLowerCase();

                    // console.log(id, departmentName, regionName, csm, sale)

                    if (!data[csm]) {
                        data[csm] = {}
                        this.setState({csm: this.state.csm.push(csm)})
                    }
                    if (!data[csm][sale]) {
                        data[csm][sale] = []
                    }
                    data[csm][sale].push({id, departmentName, regionName})
                })

                Object.keys(data).forEach((key,i) => {
                    Object.keys(data[key]).forEach((subKey, j) => {
                        data[key][subKey] = {
                            dep: data[key][subKey],
                            color: this.state.colors[i][j]
                        }
                    })
                });
                this.setState({data: data}, () => {
                    // console.log(this.state.data)
                })


            } else {
                this.setState({error: 'problème dans la lecture du fichier'})
            }
        } catch (error) {
            this.setState({error: error.toString()});
        }
    }

    save() {
        if (this.state.data) {
            localStorage.setItem('csmMotork', JSON.stringify(this.state));
        }
    }
    
    reset() {
        this.setState(
            {
                data : {},
                list: list,
                error: null,
                csm: [],
                selectedDep: null,
                depName: null
            }
        );
        localStorage.removeItem("csmMotork");
        const input = document.getElementById('fileInput');
        if (input) {
            input.value = null;
        }
    }

    componentDidMount() {
        if (localStorage.getItem('csmMotork')) {
            this.setState(JSON.parse(localStorage.getItem('csmMotork')))
        }
    }

    handleSelectedDep(dep) {
        this.setState({selectedDep: dep, depName: this.state.list.filter(itm => {
                return itm.id === 'FR-'+dep
            })[0].title})
    }

    getStyle() {
        let style = '';
        if (this.state.selectedDep) {
            style += `#FR-${this.state.selectedDep} {stroke-width: 3px}`;
            style += `#L-FR-${this.state.selectedDep} {font-weight: 900; color: red}`;
        }
        return style;
    }
    render()
    {
        return (
            <>
                <div>
                    <input type="file" id="fileInput" onChange={this.readXlsFile.bind(this)}/>
                    <input type="button" id="save" value="Save" onClick={this.save.bind(this)}/>
                    <input type="button" id="reset" value="Reset" onClick={this.reset.bind(this)}/>

                </div>
                <div className="france-wrapper">
                    <France onSelectDep={this.handleSelectedDep}/>
                    <Caption data={this.state.data}/>
                    {this.state.depName && (
                        <>{this.state.depName} - {this.state.selectedDep}</>
                    )}
                </div>
                <div className="departments-list">
                    <DepartmentsList list={this.state.list} onSelectDep={this.handleSelectedDep}/>
                    <ComputedStyle data={this.state.data} />
                </div>
                <style>
                    {this.getStyle()}
                </style>
            </>
        );
    }



}

export default App;
