import React, {Component} from 'react';

class Caption extends Component {
    constructor(props) {
        super(props);
        // console.log(this.props.data);
    }
    getStyle() {
        let style = ``;
        // console.log(this.props.data);
        Object.keys(this.props.data).forEach((key,i) => {
            style += `<h2>${key}</h2>`;
            style += `<ul>`
            // console.log(key)
            // console.log(this.props.data[key])
            Object.keys(this.props.data[key]).forEach((subKey, j) => {
                style += `<li style="color: ${this.props.data[key][subKey].color}"><span class="caption-color" style="background-color: ${this.props.data[key][subKey].color}"></span> ${subKey}</li>`
            })
            style += `</ul>`
        });
        return style
    }
    render()
    {
        return(
            <div dangerouslySetInnerHTML={{__html: this.getStyle()}} />
        )
    }

}
export default Caption;