import React, {Component} from 'react';

class DepartmentsList extends Component {
    constructor(props) {
        super(props);
    }

    handleSelectedDep(e) {
        this.props.onSelectDep(e.target.id.replace('L-FR-',''))
    }

    render()
    {
            return(
                this.props.list.map(item =>
                    <p onClick={this.handleSelectedDep.bind(this)} className="departments-list__item"  key={item.id} id={`L-${item.id}`}><strong>{item.id}:</strong> {item.title}</p>
                )
            )
    }

}
export default DepartmentsList;